import React, { useState, useEffect } from 'react';
import { Pencil, Save, Loader2, X, KeyRound } from 'lucide-react';
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';
import authUtils from '../utils/authUtils';
import { useNavigate } from 'react-router-dom';

const MODULES = [
    "Danh sách bàn",
    "Danh sách nhân viên",
    "Danh sách Sản phẩm",
    "Bán hàng"
];

const getImageUrl = (imagePath) => {
    if (!imagePath) return '';

    const path = String(imagePath);

    if (path.startsWith('http') || path.startsWith('data:')) return path;

    if (path.startsWith('DSNV_Images/')) {
        const appName = encodeURIComponent('AppHome-668554821');
        const tableName = encodeURIComponent('DSNV');
        const fileName = encodeURIComponent(path);
        return `https://www.appsheet.com/template/gettablefileurl?appName=${appName}&tableName=${tableName}&fileName=${fileName}`;
    }

    return path;
};

const Profile = () => {
    const [loading, setLoading] = useState(true);
    const [editing, setEditing] = useState(false);
    const [changePassword, setChangePassword] = useState(false);
    const [isSubmitting, setIsSubmitting] = useState(false);
    const [isChangingPassword, setIsChangingPassword] = useState(false);
    const [userData, setUserData] = useState(null);
    const [imagePreview, setImagePreview] = useState('');
    const [imageFile, setImageFile] = useState(null);
    const [formData, setFormData] = useState({
        'Họ và Tên': '',
        'Chức vụ': '',
        'Phòng': '',
        'Email': '',
        'Image': '',
    });
    const [passwordData, setPasswordData] = useState({
        currentPassword: '',
        newPassword: '',
        confirmPassword: '',
    });
    const navigate = useNavigate();

    useEffect(() => {
        const fetchUserData = async () => {
            try {
                const localUser = authUtils.getUserData();
                const response = await authUtils.apiRequest('DSNV', 'Find', {
                    Properties: {
                        Selector: `Filter(DSNV, and( [username] = "${localUser.username}" , [password] = "${localUser.password}"))`
                    }
                });

                const user = response[0];
                if (!user) {
                    throw new Error('Không tìm thấy thông tin người dùng');
                }

                setUserData(user);
                setFormData({
                    'Họ và Tên': user['Họ và Tên'] || '',
                    'Chức vụ': user['Chức vụ'] || '',
                    'Phòng': user['Phòng'] || '',
                    'Email': user['Email'] || '',
                    'Image': user['Image'] || '',
                });

                // Set initial image preview if exists
                if (user['Image']) {
                    setImagePreview(getImageUrl(user['Image']));
                }
            } catch (error) {
                console.error('Error fetching user data:', error);
                toast.error('Không thể tải thông tin người dùng');
            } finally {
                setLoading(false);
            }
        };

        fetchUserData();
    }, []);

    const handleImageChange = async (e) => {
        const file = e.target.files[0];
        if (!file) return;

        try {
            // Validate file
            const validation = authUtils.validateImage(file);
            if (!validation.isValid) {
                toast.error(validation.errors[0]);
                return;
            }

            // Create preview
            const base64Preview = await authUtils.getImageAsBase64(file);
            setImagePreview(base64Preview);
            setImageFile(file);

        } catch (error) {
            console.error('Error handling image:', error);
            toast.error('Không thể xử lý file ảnh');
            setImagePreview('');
            setImageFile(null);
            setFormData(prev => ({
                ...prev,
                'Image': ''
            }));
        }
    };

    const handleInputChange = (e) => {
        setFormData({
            ...formData,
            [e.target.name]: e.target.value
        });
    };

    const handlePasswordChange = (e) => {
        setPasswordData({
            ...passwordData,
            [e.target.name]: e.target.value
        });
    };

    const handleSubmit = async () => {
        if (isSubmitting) return;

        try {
            setIsSubmitting(true);
            setLoading(true);

            let imageUrl = formData['Image'];
            // Upload image if new image was selected
            if (imageFile) {
                try {
                    const uploadResult = await authUtils.uploadImage(imageFile);
                    if (uploadResult.success) {
                        imageUrl = uploadResult.url;
                    } else {
                        throw new Error('Upload failed');
                    }
                } catch (error) {
                    toast.error('Không thể tải ảnh lên');
                    return;
                }
            }

            // Update user data
            const updatedData = {
                ...userData,
                ...formData,
                'Image': imageUrl,
            };

            await authUtils.apiRequest('DSNV', 'Edit', {
                Rows: [updatedData]
            }, {
                "Properties": {}
            });

            authUtils.saveAuthData(updatedData);

            toast.success('Cập nhật thông tin thành công');
            setEditing(false);
            // Reset image states only if successful
            if (!imageFile) {
                setImagePreview('');
                setImageFile(null);
            }
        } catch (error) {
            console.error('Update failed:', error);
            toast.error('Cập nhật thông tin thất bại');
        } finally {
            setIsSubmitting(false);
            setLoading(false);
        }
    };

    const handleLogout = () => {
        authUtils.logout();
        navigate('/');
    };

    const handlePasswordSubmit = async () => {
        if (isChangingPassword) return;

        if (passwordData.newPassword !== passwordData.confirmPassword) {
            toast.error('Mật khẩu mới không khớp');
            return;
        }

        try {
            setIsChangingPassword(true);
            setLoading(true);

            await authUtils.apiRequest('DSNV', 'Edit', {
                Rows: [{
                    ...userData,
                    password: passwordData.newPassword
                }]
            }, {
                "Properties": {}
            });

            toast.success('Đổi mật khẩu thành công');
            setChangePassword(false);
            setPasswordData({
                currentPassword: '',
                newPassword: '',
                confirmPassword: '',
            });
            handleLogout()
        } catch (error) {
            toast.error('Đổi mật khẩu thất bại');
        } finally {
            setIsChangingPassword(false);
            setLoading(false);
        }
    };

    if (loading) {
        return (
            <div className="flex justify-center items-center min-h-screen">
                <Loader2 className="w-8 h-8 animate-spin text-blue-500" />
            </div>
        );
    }

    return (
        <div className="p-6 max-w-7xl mx-auto">
            <div className="bg-white rounded-xl shadow-sm">
                <div className="p-6">
                    <div className="grid grid-cols-1 md:grid-cols-4 gap-6">
                        {/* Profile Sidebar */}
                        <div className="md:col-span-1">
                            <div className="flex flex-col items-center">
                                <div className="relative group">
                                    {editing ? (
                                        <div className="w-32 h-32 relative">
                                            {imagePreview || formData['Image'] ? (
                                                <img
                                                    src={imagePreview || getImageUrl(formData['Image'])}
                                                    alt="Profile"
                                                    className="w-full h-full rounded-full object-cover"
                                                    onError={() => {
                                                        // Thay đổi cách xử lý lỗi ảnh
                                                        setImagePreview('');
                                                        setFormData(prev => ({ ...prev, 'Image': '' }));
                                                    }}
                                                />
                                            ) : (
                                                <div className="w-32 h-32 rounded-full bg-blue-500 flex items-center justify-center text-white text-4xl font-semibold">
                                                    {userData?.username?.[0]?.toUpperCase()}
                                                </div>
                                            )}
                                            <label className="absolute inset-0 flex items-center justify-center bg-black bg-opacity-50 rounded-full cursor-pointer opacity-0 group-hover:opacity-100 transition-opacity">
                                                <input
                                                    type="file"
                                                    accept="image/*"
                                                    className="hidden"
                                                    onChange={handleImageChange}
                                                />
                                                <span className="text-white text-sm">Thay đổi ảnh</span>
                                            </label>
                                        </div>
                                    ) : (
                                        formData['Image'] ? (
                                            <img
                                                src={getImageUrl(formData['Image'])}
                                                alt="Profile"
                                                className="w-32 h-32 rounded-full object-cover"
                                                onError={() => {
                                                    // Thay đổi cách xử lý lỗi ảnh
                                                    setFormData(prev => ({ ...prev, 'Image': '' }));
                                                }}
                                            />
                                        ) : (
                                            <div className="w-32 h-32 rounded-full bg-blue-500 flex items-center justify-center text-white text-4xl font-semibold">
                                                {userData?.username?.[0]?.toUpperCase()}
                                            </div>
                                        )
                                    )}
                                </div>
                                <h3 className="mt-4 text-xl font-semibold text-gray-800">
                                    {formData['Họ và Tên'] || userData?.username}
                                </h3>
                                <p className="text-gray-500 mt-1">
                                    {formData['Chức vụ']}
                                </p>
                                <button
                                    onClick={() => setChangePassword(true)}
                                    disabled={isSubmitting}
                                    className="mt-4 inline-flex items-center px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50 disabled:opacity-50 disabled:cursor-not-allowed"
                                >
                                    <KeyRound className="w-4 h-4 mr-2" />
                                    Đổi mật khẩu
                                </button>
                            </div>
                        </div>

                        {/* Profile Content */}
                        <div className="md:col-span-3">
                            <div className="flex justify-between items-center mb-6">
                                <h2 className="text-xl font-semibold text-gray-800">
                                    Thông tin cá nhân
                                </h2>
                                <button
                                    onClick={() => editing ? handleSubmit() : setEditing(true)}
                                    disabled={isSubmitting}
                                    className={`inline-flex items-center px-4 py-2 rounded-lg text-sm font-medium ${editing
                                        ? 'bg-green-500 hover:bg-green-600 text-white'
                                        : 'bg-blue-500 hover:bg-blue-600 text-white'
                                        } ${isSubmitting ? 'opacity-50 cursor-not-allowed' : ''}`}
                                >
                                    {editing ? (
                                        <>
                                            {isSubmitting ? (
                                                <>
                                                    <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                                                    Đang lưu...
                                                </>
                                            ) : (
                                                <>
                                                    <Save className="w-4 h-4 mr-2" />
                                                    Lưu
                                                </>
                                            )}
                                        </>
                                    ) : (
                                        <>
                                            <Pencil className="w-4 h-4 mr-2" />
                                            Chỉnh sửa
                                        </>
                                    )}
                                </button>
                            </div>

                            <div className="grid grid-cols-1 sm:grid-cols-2 gap-4">
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-1">
                                        Họ và tên
                                    </label>
                                    <input
                                        type="text"
                                        name="Họ và Tên"
                                        value={formData['Họ và Tên']}
                                        onChange={handleInputChange}
                                        disabled={!editing || isSubmitting}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-50"
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-1">
                                        Email
                                    </label>
                                    <input
                                        type="email"
                                        name="Email"
                                        value={formData['Email']}
                                        onChange={handleInputChange}
                                        disabled={!editing || isSubmitting}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-50"
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-1">
                                        Chức vụ
                                    </label>
                                    <input
                                        type="text"
                                        name="Chức vụ"
                                        value={formData['Chức vụ']}
                                        onChange={handleInputChange}
                                        disabled={!editing || isSubmitting}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-50"
                                    />
                                </div>
                                <div>
                                    <label className="block text-sm font-medium text-gray-700 mb-1">
                                        Phòng
                                    </label>
                                    <input
                                        type="text"
                                        name="Phòng"
                                        value={formData['Phòng']}
                                        onChange={handleInputChange}
                                        disabled={!editing || isSubmitting}
                                        className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-50"
                                    />
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            {/* Change Password Modal */}
            {changePassword && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-white rounded-xl shadow-lg max-w-md w-full mx-4">
                        <div className="flex justify-between items-center p-4 border-b">
                            <h3 className="text-lg font-semibold text-gray-800">Đổi mật khẩu</h3>
                            <button
                                onClick={() => setChangePassword(false)}
                                disabled={isChangingPassword}
                                className="text-gray-500 hover:text-gray-700 disabled:opacity-50"
                            >
                                <X className="w-5 h-5" />
                            </button>
                        </div>

                        <div className="p-4 space-y-4">
                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                    Mật khẩu hiện tại
                                </label>
                                <input
                                    type="password"
                                    name="currentPassword"
                                    value={passwordData.currentPassword}
                                    onChange={handlePasswordChange}
                                    disabled={isChangingPassword}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-50"
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                    Mật khẩu mới
                                </label>
                                <input
                                    type="password"
                                    name="newPassword"
                                    value={passwordData.newPassword}
                                    onChange={handlePasswordChange}
                                    disabled={isChangingPassword}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-50"
                                />
                            </div>

                            <div>
                                <label className="block text-sm font-medium text-gray-700 mb-1">
                                    Xác nhận mật khẩu mới
                                </label>
                                <input
                                    type="password"
                                    name="confirmPassword"
                                    value={passwordData.confirmPassword}
                                    onChange={handlePasswordChange}
                                    disabled={isChangingPassword}
                                    className="w-full px-3 py-2 border border-gray-300 rounded-lg focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:bg-gray-50"
                                />
                            </div>
                        </div>

                        <div className="p-4 border-t flex justify-end space-x-2">
                            <button
                                onClick={() => setChangePassword(false)}
                                disabled={isChangingPassword}
                                className={`px-4 py-2 border border-gray-300 rounded-lg text-sm font-medium text-gray-700 hover:bg-gray-50 ${isChangingPassword ? 'opacity-50 cursor-not-allowed' : ''}`}
                            >
                                Hủy
                            </button>
                            <button
                                onClick={handlePasswordSubmit}
                                disabled={isChangingPassword}
                                className={`px-4 py-2 bg-blue-500 text-white rounded-lg text-sm font-medium hover:bg-blue-600 flex items-center ${isChangingPassword ? 'opacity-50 cursor-not-allowed' : ''}`}
                            >
                                {isChangingPassword ? (
                                    <>
                                        <Loader2 className="w-4 h-4 mr-2 animate-spin" />
                                        Đang xử lý...
                                    </>
                                ) : (
                                    'Xác nhận'
                                )}
                            </button>
                        </div>
                    </div>
                </div>
            )}

            {/* Toast Container */}
            <div className="relative z-50">
                <ToastContainer
                    position="top-right"
                    autoClose={3000}
                    hideProgressBar={false}
                    newestOnTop
                    closeOnClick
                    rtl={false}
                    pauseOnFocusLoss
                    draggable
                    pauseOnHover
                />
            </div>
        </div>
    );
};

export default Profile;