// App.js
import React from 'react';
import { BrowserRouter, Routes, Route, Navigate, useLocation } from 'react-router-dom';
import { ToastContainer } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

import LoginPage from './pages/LoginPage';
import Dashboard from './pages/Dashboard';
import MainLayout from './layouts/MainLayout';
import authUtils from './utils/authUtils';
import Profile from './pages/Profile';
import UnauthorizedPage from './pages/UnauthorizedPage';
import EmployeeManagement from './pages/EmployeeManagement';
import NotFoundPage from './pages/NotFoundPage';
import KHCNManagement from './pages/KHCNManagement';
import CSKHManagement from './pages/CSKHManagement';
import ClassManagement from './pages/ClassManagement';
import ReceiptManagement from './pages/ReceiptManagement';

const ProtectedRoute = ({ children }) => {
  const location = useLocation();
  const isAuth = authUtils.isAuthenticated(location.pathname);

  if (!isAuth) {
    return <Navigate to="" state={{ from: location.pathname }} />;
  }

  return children;
};

const AdminRoute = ({ children }) => {
  const location = useLocation();
  if (!authUtils.isAdmin()) {
    return <Navigate to="unauthorized" state={{ from: location.pathname }} />;
  }
  return children;
};

function App() {
  return (
    <BrowserRouter basename="/LeaAdmin">
      <ToastContainer position="top-right" />
      <Routes>
        <Route path="" element={<LoginPage />} />
        <Route
          path="*"
          element={
            <ProtectedRoute>
              <MainLayout>
                <Routes>
                  <Route path="dashboard" element={<Dashboard />} />
                  <Route path="profile" element={<Profile />} />
                  <Route path="unauthorized" element={<UnauthorizedPage />} />
                  <Route path="khcn-management" element={<KHCNManagement />} />
                  <Route path="cskh-management" element={<CSKHManagement />} />
                  <Route path="class-management" element={<ClassManagement />} />
                  <Route path="receipt-management" element={<ReceiptManagement />} />
                  <Route
                    path="employee-management"
                    element={
                      <AdminRoute>
                        <EmployeeManagement />
                      </AdminRoute>
                    }
                  />
                  <Route path="" element={<Navigate to="dashboard" replace />} />
                  <Route path="*" element={<NotFoundPage />} />
                </Routes>
              </MainLayout>
            </ProtectedRoute>
          }
        />
      </Routes>
    </BrowserRouter>
  );
}

export default App;

