import React, { useState, useEffect } from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import { useNavigate } from 'react-router-dom';
import authUtils from '../utils/authUtils';

const Dashboard = () => {
    const navigate = useNavigate();
    const [isLoading, setIsLoading] = useState(true);

    // State cho các chỉ số
    const [stats, setStats] = useState({
        totalStudents: 0,
        activeStudents: 0,
        newLeads: 0,
        potentialLeads: 0,
        totalCare: 0,
        totalRevenue: 0,
        totalClass: 0
    });

    // State cho các biểu đồ
    const [pieChartOptions, setPieChartOptions] = useState({});
    const [customerChartOptions, setCustomerChartOptions] = useState({});
    const [revenueChart, setRevenueChart] = useState({});
    const [careChart, setCareChart] = useState({});
    const [classChart, setClassChart] = useState({});
    const [salesChartOptions, setSalesChartOptions] = useState({});
    const [sourceChartOptions, setSourceChartOptions] = useState({});
    const [needsChartOptions, setNeedsChartOptions] = useState({});
    const [studentStatusChart, setStudentStatusChart] = useState({});

    useEffect(() => {
        fetchDashboardData();
    }, []);

    const fetchDashboardData = async () => {
        setIsLoading(true);
        try {
            const [khcnData, cskhData, phieuThuData, lopData] = await Promise.all([
                authUtils.apiRequest('KHCN', 'Find', { Properties: { Selector: 'Filter(KHCN , true)' } }),
                authUtils.apiRequest('CSKH', 'Find', { Properties: { Selector: 'Filter(CSKH , true)' } }),
                authUtils.apiRequest('PhieuThu', 'Find', { Properties: { Selector: 'Filter(PhieuThu , true)' } }),
                authUtils.apiRequest('Lop', 'Find', { Properties: { Selector: 'Filter(Lop , true)' } })
            ]);

            if (khcnData) {
                const stats = {
                    totalStudents: khcnData.filter(s => s['Tình trạng'] === 'Đã thành khách hàng').length,
                    activeStudents: khcnData.filter(s => s['Trạng thái ưu đãi'] === 'Đang áp dụng').length,
                    newLeads: khcnData.filter(s => new Date(s['Ngày ghi nhận']).getMonth() === new Date().getMonth()).length,
                    potentialLeads: khcnData.filter(s => s['Tình trạng'] === 'Khách hàng tiềm năng').length,
                    totalCare: cskhData?.length || 0,
                    totalRevenue: phieuThuData?.reduce((sum, item) => sum + (parseFloat(item['Số tiền thanh toán']) || 0), 0),
                    totalClass: lopData?.length || 0
                };
                setStats(stats);
                updateCharts(khcnData, cskhData, phieuThuData, lopData);
            }
        } catch (error) {
            console.error('Failed to fetch data:', error);
        } finally {
            setIsLoading(false);
        }
    };

    const updateCharts = (khcnData, cskhData, phieuThuData, lopData) => {
        // Cập nhật biểu đồ phân bố trạng thái
        const statusDistribution = khcnData.reduce((acc, curr) => {
            acc[curr['Tình trạng']] = (acc[curr['Tình trạng']] || 0) + 1;
            return acc;
        }, {});

        setPieChartOptions({
            chart: { type: 'pie' },
            title: { text: 'Phân bố trạng thái học viên' },
            plotOptions: {
                pie: {
                    dataLabels: {
                        enabled: true,
                        format: '<b>{point.name}</b>: {point.percentage:.1f}%'
                    }
                }
            },
            series: [{
                name: 'Số lượng',
                data: Object.entries(statusDistribution).map(([name, y]) => ({ name, y }))
            }]
        });

        // Cập nhật biểu đồ khách hàng theo tháng/năm
        const monthYearData = khcnData.reduce((acc, curr) => {
            const date = new Date(curr['Ngày ghi nhận']);
            const key = `${date.getMonth() + 1}/${date.getFullYear()}`;
            if (!acc[key]) {
                acc[key] = {
                    potential: 0,
                    converted: 0
                };
            }
            if (curr['Tình trạng'] === 'Khách hàng tiềm năng') {
                acc[key].potential++;
            } else if (curr['Tình trạng'] === 'Đã thành khách hàng') {
                acc[key].converted++;
            }
            return acc;
        }, {});

        const monthYears = Object.keys(monthYearData).sort((a, b) => {
            const [m1, y1] = a.split('/').map(Number);
            const [m2, y2] = b.split('/').map(Number);
            return y1 - y2 || m1 - m2;
        });

        setCustomerChartOptions({
            chart: { type: 'column' },
            title: { text: 'Thống kê khách hàng theo thời gian' },
            xAxis: {
                categories: monthYears,
                title: { text: 'Tháng/Năm' }
            },
            yAxis: {
                title: { text: 'Số lượng' },
                stackLabels: { enabled: true }
            },
            plotOptions: {
                column: {
                    stacking: 'normal',
                    dataLabels: { enabled: true }
                }
            },
            series: [
                {
                    name: 'Khách hàng tiềm năng',
                    data: monthYears.map(key => monthYearData[key].potential)
                },
                {
                    name: 'Đã thành khách hàng',
                    data: monthYears.map(key => monthYearData[key].converted)
                }
            ]
        });

        // Biểu đồ doanh thu
        const revenueByMonth = phieuThuData.reduce((acc, curr) => {
            const date = new Date(curr['Ngày chứng từ']);
            const key = `${date.getMonth() + 1}/${date.getFullYear()}`;
            acc[key] = (acc[key] || 0) + (parseFloat(curr['Số tiền thanh toán']) || 0);
            return acc;
        }, {});

        setRevenueChart({
            chart: { type: 'column' },
            title: { text: 'Doanh thu theo thời gian' },
            xAxis: {
                categories: Object.keys(revenueByMonth).sort((a, b) => {
                    const [m1, y1] = a.split('/');
                    const [m2, y2] = b.split('/');
                    return y1 - y2 || m1 - m2;
                })
            },
            yAxis: { title: { text: 'Doanh thu' } },
            plotOptions: {
                column: {
                    dataLabels: {
                        enabled: true,
                        format: '{point.y:,.0f} đ'
                    }
                }
            },
            series: [{
                name: 'Doanh thu',
                data: Object.values(revenueByMonth)
            }]
        });

        // Biểu đồ CSKH
        const careByType = cskhData.reduce((acc, curr) => {
            const type = curr['Loại chăm sóc'];
            acc[type] = (acc[type] || 0) + 1;
            return acc;
        }, {});

        setCareChart({
            chart: { type: 'pie' },
            title: { text: 'Phân bố loại chăm sóc' },
            plotOptions: {
                pie: {
                    dataLabels: {
                        enabled: true,
                        format: '<b>{point.name}</b>: {point.percentage:.1f}%'
                    }
                }
            },
            series: [{
                name: 'Số lượng',
                data: Object.entries(careByType).map(([name, y]) => ({ name, y }))
            }]
        });

        // Biểu đồ lớp học
        const classByStatus = lopData.reduce((acc, curr) => {
            const status = curr['Duyệt khai giảng'] || 'Chưa duyệt';
            acc[status] = (acc[status] || 0) + 1;
            return acc;
        }, {});

        setClassChart({
            chart: { type: 'column' },
            title: { text: 'Thống kê lớp học' },
            xAxis: { categories: Object.keys(classByStatus) },
            yAxis: { title: { text: 'Số lượng' } },
            plotOptions: {
                column: {
                    dataLabels: { enabled: true }
                }
            },
            series: [{
                name: 'Số lớp',
                data: Object.values(classByStatus)
            }]
        });

        // Biểu đồ Sales
        const salesStats = khcnData.reduce((acc, curr) => {
            const sales = curr['Sales Phụ Trách'];
            if (!acc[sales]) {
                acc[sales] = {
                    potential: 0,
                    converted: 0
                };
            }
            if (curr['Tình trạng'] === 'Khách hàng tiềm năng') {
                acc[sales].potential++;
            } else if (curr['Tình trạng'] === 'Đã thành khách hàng') {
                acc[sales].converted++;
            }
            return acc;
        }, {});

        setSalesChartOptions({
            chart: { type: 'column' },
            title: { text: 'Thống kê theo Sales phụ trách' },
            xAxis: { categories: Object.keys(salesStats) },
            yAxis: { title: { text: 'Số lượng' } },
            plotOptions: {
                column: {
                    dataLabels: { enabled: true }
                }
            },
            series: [
                {
                    name: 'Khách hàng tiềm năng',
                    data: Object.values(salesStats).map(item => item.potential)
                },
                {
                    name: 'Đã thành khách hàng',
                    data: Object.values(salesStats).map(item => item.converted)
                }
            ]
        });

        // Biểu đồ nguồn tiếp nhận
        const sourceStats = khcnData.reduce((acc, curr) => {
            const source = curr['Nguồn tiếp nhận'];
            if (!acc[source]) acc[source] = 0;
            acc[source]++;
            return acc;
        }, {});

        setSourceChartOptions({
            chart: { type: 'pie' },
            title: { text: 'Phân bố theo nguồn tiếp nhận' },
            plotOptions: {
                pie: {
                    dataLabels: {
                        enabled: true,
                        format: '<b>{point.name}</b>: {point.percentage:.1f}%'
                    }
                }
            },
            series: [{
                name: 'Số lượng',
                data: Object.entries(sourceStats).map(([name, y]) => ({ name, y }))
            }]
        });

        // Biểu đồ nhu cầu
        const needsStats = khcnData.reduce((acc, curr) => {
            const need = curr['Nhu cầu'];
            if (!acc[need]) acc[need] = 0;
            acc[need]++;
            return acc;
        }, {});

        setNeedsChartOptions({
            chart: { type: 'pie' },
            title: { text: 'Phân bố theo nhu cầu' },
            plotOptions: {
                pie: {
                    dataLabels: {
                        enabled: true,
                        format: '<b>{point.name}</b>: {point.percentage:.1f}%'
                    }
                }
            },
            series: [{
                name: 'Số lượng',
                data: Object.entries(needsStats).map(([name, y]) => ({ name, y }))
            }]
        });
    };

    return (
        <div className="p-6 bg-gray-50 min-h-screen">
            <div className="max-w-9xl mx-auto space-y-6">
                {/* Stats */}
                <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                    {Object.entries(stats).map(([key, value]) => (
                        <div key={key} className="bg-white p-6 rounded-lg shadow-lg hover:shadow-xl transition-shadow duration-300">
                            {isLoading ? (
                                <div className="animate-pulse">
                                    <div className="h-4 bg-gray-200 rounded w-3/4"></div>
                                    <div className="h-8 bg-gray-200 rounded w-1/2 mt-4"></div>
                                </div>
                            ) : (
                                <>
                                    <h2 className="text-lg font-semibold text-gray-700 mb-2">
                                        {key === 'totalStudents' ? 'Tổng khách hàng' :
                                            key === 'activeStudents' ? 'Học viên đang học' :
                                                key === 'newLeads' ? 'Lead mới trong tháng' :
                                                    key === 'potentialLeads' ? 'Lead tiềm năng' :
                                                        key === 'totalCare' ? 'Tổng số lần CSKH' :
                                                            key === 'totalRevenue' ? 'Tổng doanh thu' :
                                                                'Tổng số lớp'}
                                    </h2>
                                    <p className="text-3xl font-bold text-blue-600">
                                        {key === 'totalRevenue' ?
                                            new Intl.NumberFormat('vi-VN', { style: 'currency', currency: 'VND' }).format(value) :
                                            value}
                                    </p>
                                </>
                            )}
                        </div>
                    ))}
                </div>

                {/* Biểu đồ chính */}
                <div className="grid grid-cols-1 gap-6">
                    <div className="bg-white p-4 rounded-lg shadow">
                        {isLoading ? (
                            <div className="animate-pulse">
                                <div className="h-64 bg-gray-200 rounded"></div>
                            </div>
                        ) : (
                            <HighchartsReact highcharts={Highcharts} options={revenueChart} />
                        )}
                    </div>
                    <div className="bg-white p-4 rounded-lg shadow">
                        {isLoading ? (
                            <div className="animate-pulse">
                                <div className="h-64 bg-gray-200 rounded"></div>
                            </div>
                        ) : (
                            <HighchartsReact highcharts={Highcharts} options={customerChartOptions} />
                        )}
                    </div>
                </div>

                {/* Biểu đồ phụ */}
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                    <div className="bg-white p-4 rounded-lg shadow">
                        {isLoading ? (
                            <div className="animate-pulse">
                                <div className="h-64 bg-gray-200 rounded"></div>
                            </div>
                        ) : (
                            <HighchartsReact highcharts={Highcharts} options={careChart} />
                        )}
                    </div>
                    <div className="bg-white p-4 rounded-lg shadow">
                        {isLoading ? (
                            <div className="animate-pulse">
                                <div className="h-64 bg-gray-200 rounded"></div>
                            </div>
                        ) : (
                            <HighchartsReact highcharts={Highcharts} options={classChart} />
                        )}
                    </div>
                </div>

                {/* Biểu đồ bổ sung */}
                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                    <div className="bg-white p-4 rounded-lg shadow">
                        {isLoading ? (
                            <div className="animate-pulse">
                                <div className="h-64 bg-gray-200 rounded"></div>
                            </div>
                        ) : (
                            <HighchartsReact highcharts={Highcharts} options={pieChartOptions} />
                        )}
                    </div>
                    <div className="bg-white p-4 rounded-lg shadow">
                        {isLoading ? (
                            <div className="animate-pulse">
                                <div className="h-64 bg-gray-200 rounded"></div>
                            </div>
                        ) : (
                            <HighchartsReact highcharts={Highcharts} options={salesChartOptions} />
                        )}
                    </div>
                </div>

                <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                    <div className="bg-white p-4 rounded-lg shadow">
                        {isLoading ? (
                            <div className="animate-pulse">
                                <div className="h-64 bg-gray-200 rounded"></div>
                            </div>
                        ) : (
                            <HighchartsReact highcharts={Highcharts} options={sourceChartOptions} />
                        )}
                    </div>
                    <div className="bg-white p-4 rounded-lg shadow">
                        {isLoading ? (
                            <div className="animate-pulse">
                                <div className="h-64 bg-gray-200 rounded"></div>
                            </div>
                        ) : (
                            <HighchartsReact highcharts={Highcharts} options={needsChartOptions} />
                        )}
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Dashboard;