import React from 'react';
import { useNavigate } from 'react-router-dom';
import { ShieldAlert, Home } from 'lucide-react';

const UnauthorizedPage = () => {
    const navigate = useNavigate();

    return (
        <div className="min-h-[calc(100vh-64px)] bg-gray-50 flex flex-col items-center justify-center p-4">
            <div className="max-w-md w-full bg-white rounded-lg shadow-sm p-8 text-center">
                <div className="flex justify-center mb-4">
                    <ShieldAlert className="h-16 w-16 text-red-500" />
                </div>
                
                <h1 className="text-2xl font-bold text-gray-900 mb-2">
                    Không có quyền truy cập
                </h1>
                
                <p className="text-gray-600 mb-6">
                    Bạn không có quyền truy cập vào trang này. 
                    Vui lòng liên hệ quản trị viên nếu bạn cần hỗ trợ.
                </p>

                <div className="flex justify-center gap-4">
                    <button
                        onClick={() => navigate(-1)}
                        className="px-4 py-2 text-sm font-medium text-gray-700 bg-gray-100 rounded-lg hover:bg-gray-200 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-gray-500"
                    >
                        Quay lại
                    </button>
                    
                    <button
                        onClick={() => navigate('/dashboard')}
                        className="px-4 py-2 text-sm font-medium text-white bg-blue-600 rounded-lg hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-blue-500 flex items-center"
                    >
                        <Home className="w-4 h-4 mr-2" />
                        Trang chủ
                    </button>
                </div>
            </div>
        </div>
    );
};

export default UnauthorizedPage;