import React, { useState } from 'react';
import { useNavigate, useLocation } from 'react-router-dom';
import {
    LayoutDashboard,
    User,
    Users,
    Users2,
    LogOut,
    Menu as MenuIcon,
    X,
    HeartHandshake,
    GraduationCap,
    Receipt
} from 'lucide-react';
import authUtils from '../utils/authUtils';

const MainLayout = ({ children }) => {
    const navigate = useNavigate();
    const location = useLocation();
    const [isSidebarOpen, setIsSidebarOpen] = useState(false);
    const [isProfileMenuOpen, setIsProfileMenuOpen] = useState(false);

    const userData = authUtils.getUserData();
    const isAdmin = authUtils.isAdmin(); 
    const menuItems = [
        { text: 'Tổng quan', icon: LayoutDashboard, path: '/dashboard' },
        { text: 'KHCN', icon: Users, path: '/khcn-management' },
        { text: 'CSKH', icon: HeartHandshake, path: '/cskh-management' }, 
        { text: 'Lớp học', icon: GraduationCap, path: '/class-management' },
        { text: 'Phiếu thu', icon: Receipt, path: '/receipt-management' },
        ...(isAdmin ? [
            { text: 'Quản lý nhân viên', icon: Users2, path: '/employee-management', adminOnly: true }
        ] : [])
    ];
    const handleLogout = () => {
        authUtils.logout();
        navigate('/');
    };

    const userInitial = userData?.username?.[0]?.toUpperCase() || '?';

    const Sidebar = () => (
        <div className="flex flex-col h-full bg-white">
            {/* Logo & Brand */}
            <div className="flex items-center justify-between px-6 py-4 border-b">
                <h1 className="text-xl font-semibold text-gray-800">
                    Tên app
                </h1>
                <button
                    className="lg:hidden text-gray-500 hover:text-gray-700"
                    onClick={() => setIsSidebarOpen(false)}
                >
                    <X className="h-6 w-6" />
                </button>
            </div>

            {/* User Info */}
            <div className="px-6 py-4 border-b">
                <div
                    className="flex items-center space-x-3 cursor-pointer hover:bg-gray-50 p-2 rounded-lg"
                    onClick={() => {
                        navigate('/profile');
                        setIsSidebarOpen(false);
                    }}
                >
                    <div className="w-10 h-10 rounded-full bg-blue-500 flex items-center justify-center text-white font-semibold">
                        {userInitial}
                    </div>
                    <div>
                        <p className="font-medium text-gray-800">
                            {userData?.['Họ và Tên'] || userData?.username}
                        </p>
                        <p className="text-sm text-gray-500">
                            {userData?.['Chức vụ'] || 'Nhân viên'}
                        </p>
                        {isAdmin && (
                            <span className="inline-block px-2 py-1 text-xs font-medium text-blue-600 bg-blue-100 rounded-full mt-1">
                                Admin
                            </span>
                        )}
                    </div>
                </div>
            </div>

            {/* Navigation */}
            <nav className="flex-1 px-3 py-4 space-y-1">
                {menuItems.map((item) => {
                    // Kiểm tra xem item có phải chỉ dành cho admin không
                    if (item.adminOnly && !isAdmin) return null;

                    const Icon = item.icon;
                    const isActive = location.pathname === item.path;
                    return (
                        <button
                            key={item.text}
                            onClick={() => {
                                navigate(item.path);
                                setIsSidebarOpen(false);
                            }}
                            className={`w-full flex items-center space-x-3 px-4 py-3 rounded-lg transition-colors ${
                                isActive
                                    ? 'bg-blue-50 text-blue-600'
                                    : 'text-gray-600 hover:bg-gray-50'
                            }`}
                        >
                            <Icon className={`h-5 w-5 ${isActive ? 'text-blue-500' : 'text-gray-500'}`} />
                            <span className={`font-medium ${isActive ? 'text-blue-600' : 'text-gray-700'}`}>
                                {item.text}
                            </span>
                        </button>
                    );
                })}
            </nav>
        </div>
    );
    return (
        <div className="min-h-screen bg-gray-50">
            {/* Mobile Sidebar Backdrop */}
            {isSidebarOpen && (
                <div
                    className="fixed inset-0 bg-gray-800/50 lg:hidden z-40"
                    onClick={() => setIsSidebarOpen(false)}
                />
            )}

            {/* Sidebar */}
            <aside className={`fixed top-0 left-0 h-full w-72 transform transition-transform duration-200 ease-in-out z-50 
                ${isSidebarOpen ? 'translate-x-0' : '-translate-x-full'} lg:translate-x-0 lg:z-30`}>
                <Sidebar />
            </aside>

            {/* Main Content */}
            <div className="lg:pl-72">
                {/* Header */}
                <header className="fixed top-0 right-0 left-0 lg:left-72 z-20">
                    <div className="h-16 bg-white border-b px-4 flex items-center justify-between">
                        <button
                            className="lg:hidden text-gray-500 hover:text-gray-700"
                            onClick={() => setIsSidebarOpen(true)}
                        >
                            <MenuIcon className="h-6 w-6" />
                        </button>

                        {/* User Menu */}
                        <div className="relative ml-auto">
                            <button
                                onClick={() => setIsProfileMenuOpen(!isProfileMenuOpen)}
                                className="flex items-center space-x-3 hover:bg-gray-50 rounded-lg p-2"
                            >
                                <div className="w-8 h-8 rounded-full bg-blue-500 flex items-center justify-center text-white text-sm font-medium">
                                    {userInitial}
                                </div>
                                {isAdmin && (
                                    <span className="hidden md:inline-block px-2 py-1 text-xs font-medium text-blue-600 bg-blue-100 rounded-full">
                                        Admin
                                    </span>
                                )}
                            </button>

                            {/* Dropdown Menu */}
                            {isProfileMenuOpen && (
                                <div className="absolute right-0 mt-2 w-48 bg-white rounded-lg shadow-lg border py-1">
                                    <button
                                        onClick={() => {
                                            navigate('/profile');
                                            setIsProfileMenuOpen(false);
                                        }}
                                        className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-50 flex items-center space-x-2"
                                    >
                                        <User className="h-4 w-4" />
                                        <span>Thông tin cá nhân</span>
                                    </button>
                                    <button
                                        onClick={handleLogout}
                                        className="w-full px-4 py-2 text-left text-sm text-gray-700 hover:bg-gray-50 flex items-center space-x-2"
                                    >
                                        <LogOut className="h-4 w-4" />
                                        <span>Đăng xuất</span>
                                    </button>
                                </div>
                            )}
                        </div>
                    </div>
                </header>
                {/* Page Content */}
                <main className="pt-16">
                    {children}
                </main>
            </div>
        </div>
    );
};

export default MainLayout;