import React, { useState, useEffect } from 'react';
import Highcharts from 'highcharts';
import HighchartsReact from 'highcharts-react-official';
import authUtils from '../utils/authUtils';
import { Users, TrendingUp, Filter } from 'lucide-react';
import { Tag } from 'antd';
import { Button } from 'antd';
import { Download, Eye } from 'lucide-react';
import * as XLSX from 'xlsx';
const { CheckableTag } = Tag;

function KHCNManagement() {
    const [data, setData] = useState([]);
    const [isLoading, setIsLoading] = useState(true);
    const [filters, setFilters] = useState({
        startDate: '',
        endDate: '',
        sales: [],
        sources: []
    });
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 10;
    const [newCustomerChart, setNewCustomerChart] = useState({});
    const [salesChart, setSalesChart] = useState({});
    const [sourceChart, setSourceChart] = useState({});
    const [salesList, setSalesList] = useState([]);
    const [sourcesList, setSourcesList] = useState([]);
    const [isModalVisible, setIsModalVisible] = useState(false);
    const [selectedCustomer, setSelectedCustomer] = useState(null);

    const columns = [
        {
            name: 'Mã KH',
            selector: 'IDKH',
            sortable: true,
        },
        {
            name: 'Họ và tên',
            selector: 'Tên KH',
            sortable: true,
        },
        {
            name: 'Số điện thoại',
            selector: 'Sđt',
        },
        {
            name: 'Nguồn',
            selector: 'Nguồn tiếp nhận',
        },
        {
            name: 'Sales phụ trách',
            selector: 'Sales Phụ Trách',
        },
        {
            name: 'Tình trạng',
            selector: 'Tình trạng',

            width: '180px'
        },
        {
            name: 'Ngày tạo',
            selector: 'Ngày ghi nhận',
            sortable: true,
            width: '120px'
        },
        {
            name: 'Thao tác',
            cell: row => (
                <Button
                    type="text"
                    icon={<Eye className="w-4 h-4" />}
                    onClick={() => {
                        setSelectedCustomer(row);
                        setIsModalVisible(true);
                    }}
                >
                    Chi tiết
                </Button>
            ),
            width: '100px'
        }



    ];

    const filterData = (items) => {
        return items.filter(item => {
            const date = new Date(item['Ngày ghi nhận']);
            const matchDate = (!filters.startDate || date >= new Date(filters.startDate)) &&
                (!filters.endDate || date <= new Date(filters.endDate));
            const matchSales = !filters.sales.length || filters.sales.includes(item['Sales Phụ Trách']);
            const matchSource = !filters.sources.length || filters.sources.includes(item['Nguồn tiếp nhận']);
            return matchDate && matchSales && matchSource;
        });
    };

    const getPaginatedData = (data) => {
        const startIndex = (currentPage - 1) * itemsPerPage;
        return data.slice(startIndex, startIndex + itemsPerPage);
    };

    const totalPages = Math.ceil(filterData(data).length / itemsPerPage);

    const exportToExcel = () => {
        const ws = XLSX.utils.json_to_sheet(filterData(data));
        const wb = XLSX.utils.book_new();
        XLSX.utils.book_append_sheet(wb, ws, "Customers");
        XLSX.writeFile(wb, "customers.xlsx");
    };

    const CustomerDetailModal = ({ visible, customer, onClose }) => {
        if (!visible || !customer) return null;

        const fieldGroups = {
            'Thông tin cơ bản': ['IDKH', 'Tên KH', 'Sđt', 'Ngày sinh', 'Địa chỉ', 'Email'],
            'Thông tin tiếp nhận': ['Ngày ghi nhận', 'Nguồn tiếp nhận', 'Người giới thiệu', 'Sales Phụ Trách'],
            'Thông tin học tập': ['Nhu cầu', 'Lớp', 'Trường', 'IDLOP'],
            'Trạng thái': ['Tình trạng', 'Đánh giá tiềm năng', 'Ngày trở thành KH', 'Ngày nhập học đầu tiên'],
            'Thông tin ưu đãi': ['Doanh thu lần đầu', '% UĐ', 'Trạng thái ưu đãi', 'Diện ưu đãi', 'Ngày ngừng ưu đãi', 'Lý do ngừng ưu đãi'],
            'Thông tin khác': ['Anh chị em', 'Ghi chú', 'Ghi chú lưu thông tin']
        };

        return (
            <div className="fixed inset-0 z-50 overflow-y-auto">
                <div className="flex items-center justify-center min-h-screen px-4">
                    <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" onClick={onClose}></div>

                    <div className="relative bg-white rounded-lg max-w-4xl w-full max-h-[90vh] overflow-y-auto">
                        <div className="flex items-center justify-between p-4 border-b">
                            <h3 className="text-lg font-semibold">Chi tiết khách hàng</h3>
                            <button onClick={onClose} className="text-gray-400 hover:text-gray-500">
                                <svg className="w-6 h-6" fill="none" stroke="currentColor" viewBox="0 0 24 24">
                                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M6 18L18 6M6 6l12 12" />
                                </svg>
                            </button>
                        </div>

                        <div className="p-6 space-y-6">
                            {Object.entries(fieldGroups).map(([groupName, fields]) => (
                                <div key={groupName} className="border rounded-lg p-4">
                                    <h3 className="font-medium text-gray-900 mb-3">{groupName}</h3>
                                    <div className="grid grid-cols-2 gap-4">
                                        {fields.map(field => (
                                            <div key={field} className="flex flex-col">
                                                <span className="text-sm text-gray-500">{field}</span>
                                                <span className="mt-1">{customer[field] || '--'}</span>
                                            </div>
                                        ))}
                                    </div>
                                </div>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        );
    };
    const fetchData = async () => {
        setIsLoading(true);
        try {
            const result = await authUtils.apiRequest('KHCN', 'Find', {
                Properties: { Selector: 'Filter(KHCN , true)' }
            });
            setData(result || []);
            updateCharts(result);
        } catch (error) {
            console.error('Failed to fetch data:', error);
        }
        setIsLoading(false);
    };

    const handleQuickDateSelect = (period) => {
        const today = new Date();
        let startDate, endDate;

        switch (period) {
            case 'today':
                startDate = endDate = today.toISOString().split('T')[0];
                break;
            case 'yesterday':
                startDate = endDate = new Date(today.setDate(today.getDate() - 1)).toISOString().split('T')[0];
                break;
            case 'thisMonth':
                startDate = new Date(today.getFullYear(), today.getMonth(), 1).toISOString().split('T')[0];
                endDate = new Date(today.getFullYear(), today.getMonth() + 1, 0).toISOString().split('T')[0];
                break;
            case 'lastMonth':
                startDate = new Date(today.getFullYear(), today.getMonth() - 1, 1).toISOString().split('T')[0];
                endDate = new Date(today.getFullYear(), today.getMonth(), 0).toISOString().split('T')[0];
                break;
            case 'thisYear':
                startDate = new Date(today.getFullYear(), 0, 1).toISOString().split('T')[0];
                endDate = new Date(today.getFullYear(), 11, 31).toISOString().split('T')[0];
                break;
            case 'lastYear':
                startDate = new Date(today.getFullYear() - 1, 0, 1).toISOString().split('T')[0];
                endDate = new Date(today.getFullYear(), 11, 31).toISOString().split('T')[0];
                break;
        }

        setFilters({ ...filters, startDate, endDate });
    };

    const updateCharts = (items) => {
        const filteredData = filterData(items);
        const chartCommonConfig = {
            credits: { enabled: false },
            chart: {
                backgroundColor: 'transparent',
                height: '400px',
                style: { fontFamily: 'Inter, sans-serif' }
            }
        };

        // Biểu đồ khách hàng lũy kế
        const customersByMonth = filteredData.reduce((acc, curr) => {
            const date = new Date(curr['Ngày ghi nhận']);
            const key = `${date.getMonth() + 1}/${date.getFullYear()}`;
            acc[key] = (acc[key] || 0) + 1;
            return acc;
        }, {});

        const sortedMonths = Object.keys(customersByMonth).sort((a, b) => {
            const [m1, y1] = a.split('/').map(Number);
            const [m2, y2] = b.split('/').map(Number);
            return y1 - y2 || m1 - m2;
        });

        let cumulative = 0;
        const cumulativeData = sortedMonths.map(month => {
            cumulative += customersByMonth[month];
            return cumulative;
        });

        setNewCustomerChart({
            ...chartCommonConfig,
            chart: { ...chartCommonConfig.chart, type: 'area' },
            title: { text: 'Tổng số khách hàng theo thời gian (Lũy kế)', style: { fontSize: '16px' } },
            xAxis: { categories: sortedMonths },
            yAxis: { title: { text: 'Số lượng' } },
            plotOptions: {
                area: {
                    fillColor: {
                        linearGradient: { x1: 0, y1: 0, x2: 0, y2: 1 },
                        stops: [
                            [0, 'rgba(54, 162, 235, 0.3)'],
                            [1, 'rgba(54, 162, 235, 0)']
                        ]
                    },
                    marker: { enabled: true },
                    lineWidth: 2,
                    lineColor: '#36A2EB'
                }
            },
            series: [{ name: 'Khách hàng', data: cumulativeData }]
        });

        // Biểu đồ thống kê theo Sales
        const salesStats = filteredData.reduce((acc, curr) => {
            const sales = curr['Sales Phụ Trách'];
            if (!acc[sales]) acc[sales] = { potential: 0, converted: 0 };
            curr['Tình trạng'] === 'Khách hàng tiềm năng' ? acc[sales].potential++ : acc[sales].converted++;
            return acc;
        }, {});

        setSalesChart({
            ...chartCommonConfig,
            chart: { ...chartCommonConfig.chart, type: 'column' },
            title: { text: 'Thống kê theo Sales phụ trách', style: { fontSize: '16px' } },
            xAxis: { categories: Object.keys(salesStats) },
            yAxis: { title: { text: 'Số lượng' } },
            plotOptions: { column: { borderRadius: 5, dataLabels: { enabled: true } } },
            series: [
                {
                    name: 'Khách hàng tiềm năng',
                    data: Object.values(salesStats).map(item => item.potential),
                    color: '#FFB1C1'
                },
                {
                    name: 'Đã thành khách hàng',
                    data: Object.values(salesStats).map(item => item.converted),
                    color: '#36A2EB'
                }
            ]
        });

        // Biểu đồ phân bố theo nguồn
        const sourceStats = filteredData.reduce((acc, curr) => {
            const source = curr['Nguồn tiếp nhận'];
            acc[source] = (acc[source] || 0) + 1;
            return acc;
        }, {});

        setSourceChart({
            ...chartCommonConfig,
            chart: { ...chartCommonConfig.chart, type: 'pie' },
            title: { text: 'Phân bố theo nguồn tiếp nhận', style: { fontSize: '16px' } },
            plotOptions: {
                pie: {
                    allowPointSelect: true,
                    cursor: 'pointer',
                    borderRadius: 5,
                    dataLabels: {
                        enabled: true,
                        format: '<b>{point.name}</b>: {point.percentage:.1f}%'
                    }
                }
            },
            series: [{
                name: 'Số lượng',
                colorByPoint: true,
                data: Object.entries(sourceStats).map(([name, y]) => ({ name, y }))
            }]
        });
    };

    useEffect(() => {
        fetchData();
    }, []);

    useEffect(() => {
        if (data.length) updateCharts(data);
    }, [data, filters]);

    const FilterBar = () => (
        <div className="bg-white rounded-xl shadow-sm p-6">
            <div className="flex flex-col gap-4">
                <div className="flex flex-wrap items-end gap-4">
                    <div className="flex-1 min-w-[200px]">
                        <label className="block text-sm font-medium text-gray-700 mb-2">Từ ngày</label>
                        <input
                            type="date"
                            value={filters.startDate}
                            onChange={(e) => setFilters({ ...filters, startDate: e.target.value })}
                            className="w-full border-gray-300 rounded-lg shadow-sm focus:ring-blue-500 focus:border-blue-500"
                        />
                    </div>
                    <div className="flex-1 min-w-[200px]">
                        <label className="block text-sm font-medium text-gray-700 mb-2">Đến ngày</label>
                        <input
                            type="date"
                            value={filters.endDate}
                            onChange={(e) => setFilters({ ...filters, endDate: e.target.value })}
                            className="w-full border-gray-300 rounded-lg shadow-sm focus:ring-blue-500 focus:border-blue-500"
                        />
                    </div>
                    <div className="flex gap-2">
                        <Button size="middle" onClick={() => handleQuickDateSelect('today')}>Hôm nay</Button>
                        <Button size="middle" onClick={() => handleQuickDateSelect('yesterday')}>Hôm qua</Button>
                        <Button size="middle" onClick={() => handleQuickDateSelect('thisMonth')}>Tháng này</Button>
                        <Button size="middle" onClick={() => handleQuickDateSelect('lastMonth')}>Tháng trước</Button>
                        <Button size="middle" onClick={() => handleQuickDateSelect('thisYear')}>Năm nay</Button>
                        <Button size="middle" onClick={() => handleQuickDateSelect('lastYear')}>Năm trước</Button>
                    </div>
                </div>

                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">Sales</label>
                        <div className="flex flex-wrap gap-2">
                            {salesList.map(sale => (
                                <CheckableTag
                                    key={sale}
                                    checked={filters.sales.includes(sale)}
                                    onChange={checked => {
                                        const newSales = checked ? [...filters.sales, sale] : filters.sales.filter(s => s !== sale);
                                        setFilters({ ...filters, sales: newSales });
                                    }}
                                    className={`px-3 py-1 rounded-full border transition-all ${filters.sales.includes(sale)
                                        ? 'bg-blue-50 text-blue-700 border-blue-200'
                                        : 'bg-gray-50 text-gray-600 border-gray-200'
                                        }`}
                                >
                                    {sale}
                                </CheckableTag>
                            ))}
                        </div>
                    </div>

                    <div>
                        <label className="block text-sm font-medium text-gray-700 mb-2">Nguồn</label>
                        <div className="flex flex-wrap gap-2">
                            {sourcesList.map(source => (
                                <CheckableTag
                                    key={source}
                                    checked={filters.sources.includes(source)}
                                    onChange={checked => {
                                        const newSources = checked ? [...filters.sources, source] : filters.sources.filter(s => s !== source);
                                        setFilters({ ...filters, sources: newSources });
                                    }}
                                    className={`px-3 py-1 rounded-full border transition-all ${filters.sources.includes(source)
                                        ? 'bg-blue-50 text-blue-700 border-blue-200'
                                        : 'bg-gray-50 text-gray-600 border-gray-200'
                                        }`}
                                >
                                    {source}
                                </CheckableTag>
                            ))}
                        </div>
                    </div>
                </div>
            </div>
        </div>
    );

    const SummaryCards = ({ filteredData }) => {
        const stats = {
            total: filteredData.length,
            potential: filteredData.filter(d => d['Tình trạng'] === 'Khách hàng tiềm năng').length,
            converted: filteredData.filter(d => d['Tình trạng'] === 'Đã thành khách hàng').length
        };

        return (
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
                <div className="bg-gradient-to-br from-blue-50 to-blue-100 p-6 rounded-xl shadow-sm">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-blue-600 text-sm font-semibold">Tổng số khách hàng</p>
                            <p className="text-3xl font-bold text-blue-700 mt-2">{stats.total}</p>
                        </div>
                        <Users className="w-12 h-12 text-blue-500 opacity-80" />
                    </div>
                </div>

                <div className="bg-gradient-to-br from-yellow-50 to-yellow-100 p-6 rounded-xl shadow-sm">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-yellow-600 text-sm font-semibold">Khách hàng tiềm năng</p>
                            <p className="text-3xl font-bold text-yellow-700 mt-2">{stats.potential}</p>
                        </div>
                        <Filter className="w-12 h-12 text-yellow-500 opacity-80" />
                    </div>
                </div>

                <div className="bg-gradient-to-br from-green-50 to-green-100 p-6 rounded-xl shadow-sm">
                    <div className="flex items-center justify-between">
                        <div>
                            <p className="text-green-600 text-sm font-semibold">Đã chuyển đổi</p>
                            <p className="text-3xl font-bold text-green-700 mt-2">{stats.converted}</p>
                        </div>
                        <TrendingUp className="w-12 h-12 text-green-500 opacity-80" />
                    </div>
                </div>
            </div>
        );
    };

    return (
        <div className="p-6 space-y-6 bg-gray-50 min-h-screen">
            <h1 className="text-2xl font-bold text-gray-800">Quản lý khách hàng</h1>

            <FilterBar />
            <SummaryCards filteredData={filterData(data)} />

            <div className="bg-white p-6 rounded-xl shadow-sm">
                {isLoading ? (
                    <div className="animate-pulse h-[400px] bg-gray-100 rounded-lg" />
                ) : (
                    <HighchartsReact highcharts={Highcharts} options={newCustomerChart} />
                )}
            </div>

            <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
                <div className="bg-white p-6 rounded-xl shadow-sm">
                    {isLoading ? (
                        <div className="animate-pulse h-[400px] bg-gray-100 rounded-lg" />
                    ) : (
                        <HighchartsReact highcharts={Highcharts} options={salesChart} />
                    )}
                </div>

                <div className="bg-white p-6 rounded-xl shadow-sm">
                    {isLoading ? (
                        <div className="animate-pulse h-[400px] bg-gray-100 rounded-lg" />
                    ) : (
                        <HighchartsReact highcharts={Highcharts} options={sourceChart} />
                    )}
                </div>
            </div>

            <div className="bg-white rounded-xl shadow-sm overflow-hidden">
                <div className="p-4 flex justify-between items-center">
                    <h2 className="text-lg font-semibold text-gray-800">Danh sách khách hàng</h2>
                    <Button
                        type="primary"
                        icon={<Download className="w-4 h-4" />}
                        onClick={exportToExcel}
                    >
                        Xuất Excel
                    </Button>
                </div>

                <div className="overflow-x-auto">
                    <table className="min-w-full">
                        <thead className="bg-gray-50 border-b">
                            <tr>
                                <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 tracking-wider">Mã KH</th>
                                <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 tracking-wider">Họ và tên</th>
                                <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 tracking-wider">Số điện thoại</th>
                                <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 tracking-wider">Nguồn</th>
                                <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 tracking-wider">Sales phụ trách</th>
                                <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 tracking-wider">Tình trạng</th>
                                <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 tracking-wider">Ngày tạo</th>
                                <th scope="col" className="px-4 py-3 text-left text-xs font-medium text-gray-500 tracking-wider">Thao tác</th>
                            </tr>
                        </thead>
                        <tbody className="bg-white divide-y divide-gray-200">
                            {getPaginatedData(filterData(data)).map((row, index) => (
                                <tr key={index}>
                                    {columns.map(column => (
                                        <td key={column.name}>
                                            {column.cell ? column.cell(row) : row[column.selector]}
                                        </td>
                                    ))}
                                </tr>
                            ))}
                        </tbody>
                    </table>
                </div>

                <div className="flex items-center justify-between px-4 py-3 bg-white border-t">
                    <div className="flex-1 flex justify-between items-center">
                        <button
                            onClick={() => setCurrentPage(p => Math.max(1, p - 1))}
                            disabled={currentPage === 1}
                            className="relative inline-flex items-center px-4 py-2 border text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 disabled:opacity-50"
                        >
                            Trước
                        </button>
                        <span className="text-sm text-gray-700">
                            Trang {currentPage} / {totalPages}
                        </span>
                        <button
                            onClick={() => setCurrentPage(p => Math.min(totalPages, p + 1))}
                            disabled={currentPage === totalPages}
                            className="relative inline-flex items-center px-4 py-2 border text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 disabled:opacity-50"
                        >
                            Sau
                        </button>
                    </div>
                </div>
            </div>

            <CustomerDetailModal
                visible={isModalVisible}
                customer={selectedCustomer}
                onClose={() => {
                    setIsModalVisible(false);
                    setSelectedCustomer(null);
                }}
            />
        </div>
    );
}

export default KHCNManagement;